const TABLE = require('./table');
const lunarMonthTable = TABLE.lunarMonthTable;
const holidayTable = TABLE.holidayTable;

/**
 *   ()?
 */
function isLunarHoliday(year, month, day, isLeapMonth){
  return isHoliday(year, month, day, true, isLeapMonth);
}
module.exports.isLunarHoliday = isLunarHoliday;

/**
 *   ()?
 */
function isSolarHoliday(year, month, day){
  return isHoliday(year, month, day, false, false);
}
module.exports.isSolarHoliday = isSolarHoliday;

/**
 * ,    ?( )
 * isLunar :    ?
 * isLeapMonth :    ?
 */
function isHoliday(year, month, day, isLunar, isLeapMonth){
  var date1 = splitDate(year, month, day);
  var date2 = calcLunar(date1.year, date1.month, date1.day, isLunar ? 2 : 1, isLeapMonth ? 1 : 0);
  var solarDate = isLunar ? date2 : date1;
  solarDate = new Date(solarDate.year, solarDate.month - 1, solarDate.day);
  if(solarDate.getDay() === 0)
    return true;
  else
  {
    var holiday = getHoliday(isLunar ? date2 : date1, isLunar ? date1 : date2);
    return holiday ? true : false;
  }
}
module.exports.isHoliday = isHoliday;

/**
 *      
 */
function getLunar(year, month, day){
  var date = splitDate(year, month, day);
  var o = calcLunar(date.year, date.month, date.day, 1);
  o.dayOfWeek = getDayOfWeek(date);
  return o;
}
module.exports.getLunar = getLunar;

/**
 *      
 * isLeapMonth :    ?
 */
function getSolar(year, month, day, isLeapMonth){
  var date = splitDate(year, month, day);
  var o = calcLunar(date.year, date.month, date.day, 2, isLeapMonth ? 1 : 0);
  o.dayOfWeek = getDayOfWeek(o);
  return o;
}
module.exports.getSolar = getSolar;

/**
 *       
 */
function getHoliday(solarDate, lunarDate)
{
  var i;
  var memorial;
  var addHolidays = [" ", 12, 0, 2]; //  

  if (lunarMonthTable[ solarDate.year - 1 - 1799][11] == 1)
    addHolidays[2] = 29;
  else if (lunarMonthTable[ solarDate.year - 1 - 1799][11] == 2)
    addHolidays[2] = 30;
  
  if(addHolidays[1] == lunarDate.month && addHolidays[2] == lunarDate.day)
  {
    return addHolidays;
  }
  for (i = 0; i < holidayTable.length; i++)
  {
    if (holidayTable[i][3] == 1 && holidayTable[i][1] == solarDate.month && holidayTable[i][2] == solarDate.day)
      return holidayTable[i];
    //     
    if ( lunarDate.leapMonth && lunarDate.month == 4 && lunarDate.day == 8 ) {
      return null;
    }
    if ( lunarDate.leapMonth && lunarDate.month == 8 && lunarDate.day > 13 && lunarDate.day < 17) {
      return null;
    }
    if (holidayTable[i][3] == 2 && holidayTable[i][1] == lunarDate.month && holidayTable[i][2] == lunarDate.day)
      return holidayTable[i];
  }
  return null;
}

function splitDate(year, month, day){
  if(year instanceof Date)
  {
    month = year.getMonth() + 1;
    day = year.getDate();
    year = year.getFullYear();
  }
  return {year:year, month:month, day:day};
}
function getDayOfWeek(date){
  var d = new Date(date.year, date.month-1, date.day);
  switch(d.getDay())
  {
    case 0 : return "";
    case 1 : return "";
    case 2 : return "";
    case 3 : return "";
    case 4 : return "";
    case 5 : return "";
    case 6 : return "";
  }
}

/*  <->   
 * type : 1 -  -> 
 *        2 -  -> 
 * leapmonth : 0 - 
 *             1 -  (type = 2  )
*/ 
function calcLunar(year, month, day, type, leapmonth)
{
 var solYear, solMonth, solDay;
 var lunYear, lunMonth, lunDay;
 var lunLeapMonth, lunMonthDay;
 var i, lunIndex;
 var solMonthDay = [31, 0, 31, 30, 31, 30, 31, 31, 30, 31, 30, 31]; 
 if (year < 1800 || year > 2101)
 {
  throw '1800 2101  .';
 }
 if (year >= 2080)
 {
  /*   2080 1 1 ( 2079 12 10) */
  solYear = 2080;
  solMonth = 1;
  solDay = 1;
  lunYear = 2079;
  lunMonth = 12;
  lunDay = 10;
  lunLeapMonth = 0;
  solMonthDay[1] = 29; /* 2080  2 28 */
  lunMonthDay = 30; /* 2079 12 */
 }
 else if (year >= 2060)
 {
  /*   2060 1 1 ( 2059 11 28) */
  solYear = 2060;
  solMonth = 1;
  solDay = 1;
  lunYear = 2059;
  lunMonth = 11;
  lunDay = 28;
  lunLeapMonth = 0;
  solMonthDay[1] = 29; /* 2060  2 28 */
  lunMonthDay = 30; /* 2059 11 */
 }
 else if (year >= 2040)
 {
  /*   2040 1 1 ( 2039 11 17) */
  solYear = 2040;
  solMonth = 1;
  solDay = 1;
  lunYear = 2039;
  lunMonth = 11;
  lunDay = 17;
  lunLeapMonth = 0;
  solMonthDay[1] = 29; /* 2040  2 28 */
  lunMonthDay = 29; /* 2039 11 */
 }
 else if (year >= 2020)
 {
  /*   2020 1 1 ( 2019 12 7) */
  solYear = 2020;
  solMonth = 1;
  solDay = 1;
  lunYear = 2019;
  lunMonth = 12;
  lunDay = 7;
  lunLeapMonth = 0;
  solMonthDay[1] = 29; /* 2020  2 28 */
  lunMonthDay = 30; /* 2019 12 */
 }
 else if (year >= 2000)
 {
  /*   2000 1 1 ( 1999 11 25) */
  solYear = 2000;
  solMonth = 1;
  solDay = 1;
  lunYear = 1999;
  lunMonth = 11;
  lunDay = 25;
  lunLeapMonth = 0;
  solMonthDay[1] = 29; /* 2000  2 28 */
  lunMonthDay = 30; /* 1999 11 */
 }
 else if (year >= 1980)
 {
  /*   1980 1 1 ( 1979 11 14) */
  solYear = 1980;
  solMonth = 1;
  solDay = 1;
  lunYear = 1979;
  lunMonth = 11;
  lunDay = 14;
  lunLeapMonth = 0;
  solMonthDay[1] = 29; /* 1980  2 28 */
  lunMonthDay = 30; /* 1979 11 */
 }
 else if (year >= 1960)
 {
  /*   1960 1 1 ( 1959 12 3) */
  solYear = 1960;
  solMonth = 1;
  solDay = 1;
  lunYear = 1959;
  lunMonth = 12;
  lunDay = 3;
  lunLeapMonth = 0;
  solMonthDay[1] = 29; /* 1960  2 28 */
  lunMonthDay = 29; /* 1959 12 */
 }
 else if (year >= 1940)
 {
  /*   1940 1 1 ( 1939 11 22) */
  solYear = 1940;
  solMonth = 1;
  solDay = 1;
  lunYear = 1939;
  lunMonth = 11;
  lunDay = 22;
  lunLeapMonth = 0;
  solMonthDay[1] = 29; /* 1940  2 28 */
  lunMonthDay = 29; /* 1939 11 */
 }
 else if (year >= 1920)
 {
  /*   1920 1 1 ( 1919 11 11) */
  solYear = 1920;
  solMonth = 1;
  solDay = 1;
  lunYear = 1919;
  lunMonth = 11;
  lunDay = 11;
  lunLeapMonth = 0;
  solMonthDay[1] = 29; /* 1920  2 28 */
  lunMonthDay = 30; /* 1919 11 */
 }
 else if (year >= 1900)
 {
  /*   1900 1 1 ( 1899 12 1) */
  solYear = 1900;
  solMonth = 1;
  solDay = 1;
  lunYear = 1899;
  lunMonth = 12;
  lunDay = 1;
  lunLeapMonth = 0;
  solMonthDay[1] = 28; /* 1900  2 28 */
  lunMonthDay = 30; /* 1899 12 */
 }
 else if (year >= 1880)
 {
  /*   1880 1 1 ( 1879 11 20) */
  solYear = 1880;
  solMonth = 1;
  solDay = 1;
  lunYear = 1879;
  lunMonth = 11;
  lunDay = 20;
  lunLeapMonth = 0;
  solMonthDay[1] = 29; /* 1880  2 28 */
  lunMonthDay = 30; /* 1879 11 */
 }
 else if (year >= 1860)
 {
  /*   1860 1 1 ( 1859 12 9) */
  solYear = 1860;
  solMonth = 1;
  solDay = 1;
  lunYear = 1859;
  lunMonth = 12;
  lunDay = 9;
  lunLeapMonth = 0;
  solMonthDay[1] = 29; /* 1860  2 28 */
  lunMonthDay = 30; /* 1859 12 */
 }
 else if (year >= 1840)
 {
  /*   1840 1 1 ( 1839 11 27) */
  solYear = 1840;
  solMonth = 1;
  solDay = 1;
  lunYear = 1839;
  lunMonth = 11;
  lunDay = 27;
  lunLeapMonth = 0;
  solMonthDay[1] = 29; /* 1840  2 28 */
  lunMonthDay = 30; /* 1839 11 */
 }
 else if (year >= 1820)
 {
  /*   1820 1 1 ( 1819 11 16) */
  solYear = 1820;
  solMonth = 1;
  solDay = 1;
  lunYear = 1819;
  lunMonth = 11;
  lunDay = 16;
  lunLeapMonth = 0;
  solMonthDay[1] = 29; /* 1820  2 28 */
  lunMonthDay = 30; /* 1819 11 */
 }
 else if (year >= 1800)
 {
  /*   1800 1 1 ( 1799 12 7) */
  solYear = 1800;
  solMonth = 1;
  solDay = 1;
  lunYear = 1799;
  lunMonth = 12;
  lunDay = 7;
  lunLeapMonth = 0;
  solMonthDay[1] = 28; /* 1800  2 28 */
  lunMonthDay = 30; /* 1799 12 */
 } 

 lunIndex = lunYear - 1799;

 while (true)
 {
  if (type == 1 &&
   year == solYear &&
   month == solMonth &&
   day == solDay)
  {
   return {year:lunYear, month:lunMonth, day:lunDay, leapMonth:lunLeapMonth == 1};
  } 
  else if (type == 2 &&
    year == lunYear &&
    month == lunMonth &&
    day == lunDay && 
    leapmonth == lunLeapMonth)
  {
   return {year:solYear, month:solMonth, day:solDay, leapMonth:false};
  }

  /* add a day of solar calendar */
  if (solMonth == 12 && solDay == 31)
  {
   solYear++;
   solMonth = 1;
   solDay = 1;

   /* set monthDay of Feb */
   if (solYear % 400 == 0)
    solMonthDay[1] = 29;
   else if (solYear % 100 == 0)
    solMonthDay[1] = 28;
   else if (solYear % 4 == 0)
    solMonthDay[1] = 29;
   else
    solMonthDay[1] = 28;

  }
  else if (solMonthDay[solMonth - 1] == solDay)
  {
   solMonth++;
   solDay = 1; 
  }
  else
   solDay++;

  /* add a day of lunar calendar */
  if (lunMonth == 12 &&
   ((lunarMonthTable[lunIndex][lunMonth - 1] == 1 && lunDay == 29) ||
   (lunarMonthTable[lunIndex][lunMonth - 1] == 2 && lunDay == 30)))
  {
   lunYear++;
   lunMonth = 1;
   lunDay = 1;

   if (lunYear > 2101) {
    throw "    .    .";
   }

   lunIndex = lunYear - 1799;

   if (lunarMonthTable[lunIndex][lunMonth - 1] == 1)
    lunMonthDay = 29;
   else if (lunarMonthTable[lunIndex][lunMonth - 1] == 2)
    lunMonthDay = 30;
  }
  else if (lunDay == lunMonthDay)
  {
   if (lunarMonthTable[lunIndex][lunMonth - 1] >= 3
    && lunLeapMonth == 0)
   {
    lunDay = 1;
    lunLeapMonth = 1;
   }
   else
   {
    lunMonth++;
    lunDay = 1;
    lunLeapMonth = 0;
   }

   if (lunarMonthTable[lunIndex][lunMonth - 1] == 1)
    lunMonthDay = 29;
   else if (lunarMonthTable[lunIndex][lunMonth - 1] == 2)
    lunMonthDay = 30;
   else if (lunarMonthTable[lunIndex][lunMonth - 1] == 3)
    lunMonthDay = 29;
   else if (lunarMonthTable[lunIndex][lunMonth - 1] == 4 &&
     lunLeapMonth == 0)
    lunMonthDay = 29;
   else if (lunarMonthTable[lunIndex][lunMonth - 1] == 4 &&
     lunLeapMonth == 1)
    lunMonthDay = 30;
   else if (lunarMonthTable[lunIndex][lunMonth - 1] == 5 &&
     lunLeapMonth == 0)
    lunMonthDay = 30;
   else if (lunarMonthTable[lunIndex][lunMonth - 1] == 5 &&
     lunLeapMonth == 1)
     lunMonthDay = 29;
   else if (lunarMonthTable[lunIndex][lunMonth - 1] == 6)
    lunMonthDay = 30;
  }
  else
   lunDay++;
 }
}